<script>
export default {
  data() {
    return {
      probably: [], //
      course: [], //
      courseTitle: [], //
      courses: [], //
      coursesImg: [], //
      teacher: [], //
      show: false,
      lunbo:[]
    };
  },
  mounted() {
    this.$Net.List().then((res) => {
      this.probably = res.data.data[0].list;
      this.course = res.data.data[1].list;
      this.courseTitle = res.data.data[1].list[0].teachers_list[0].teacher_name;
      this.courses = res.data.data[3].list;
      this.coursesImg =
        res.data.data[3].list[0].teachers_list[0].teacher_avatar;
      this.teacher = res.data.data[4].list;
      console.log(this.teacher);
    });
    // 
      this.$Net.LUNBO().then((res) => {
      console.log(res.data.data);
      this.lunbo=res.data.data
    });
  },
  methods: {
    Onclick(item) { 
  //  token 
      var token = localStorage.getItem("token");
      if ((token = !token)) {
        this.show = true;
        console.log(item)
      } else if ((token = !token)) {
        this.$router.push({
          path: "/homelist",
          query: {
             id:item.teacher_id
          }
        });
      }
    },
    Onpath() {
      this.$router.push({
        path: "/login",
      });
    },
    OngoA() {
      this.$router.push({
        path: "/about",
      });
    },
    OngoB() { 
      this.$router.push({
        path: "/lenderData",
        name: "LenderData"
      });
    },
    OngoC() {
      this.$router.push({
        path: "/solo",
          name: "Solo",
      
        });
    },
    // 
    hmwJumpXQ(item){
      console.log(item)
      sessionStorage.setItem('hmwXQ',JSON.stringify(item))
      sessionStorage.setItem('hmwPath',JSON.stringify({path:'/',name:'Home'}))
      this.$router.push({
        path: "/detail"
      });
    },
  },
};
</script>
